html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 18px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

._container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

._container-inner {
  max-width: 1262px;
  margin: 0 auto;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn-outline {
  color: var(--accent-color);
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  border: 2px solid var(--accent-color);
  border-radius: 12px;
  transition: all 0.3s ease 0s;
  position: relative;
  z-index: 0;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: var(--accent-color);
    border-radius: 8px;
    z-index: -1;
    cursor: pointer;
    transition: all 0.3s ease 0s;
  }
  &:hover {
    color: var(--light-color);
    &:after {
      width: 100%;
    }
  }
}

.btn-grad {
  display: inline-block;
  color: var(--light-color);
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  border-radius: 12px;
  background: linear-gradient(90deg, #F0642F 0%, #8F663E 48.97%, #306100 101.1%);
  animation: gradient 2s infinite linear;
  background-size: 170%;
  box-shadow: 0px 20px 40px 0px rgba(240, 100, 47, 0.30);
  display: inline-flex;
  align-items: center;
  transition: all 0.3s ease 0s;
  img {
    margin-left: 10px;
    transition: all 0.3s ease 0s;
  }
  &:hover {
    img {
      transform: translateX(10px);
    }
  }
}

.title {
  color: var(--main-color);
  font-size: 56px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%;
  text-transform: uppercase;
  > span {
    color: var(--accent-color);
  }
  @media (max-width: 768px){
      font-size: 44px;
  }
  @media (max-width: 480px){
      font-size: 32px;
  }
}

.title-white {
  color: var(--light-color);
  text-align: center;
  font-size: 64px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
  @media (max-width: 768px){
    font-size: 44px;
  }
  @media (max-width: 480px){
      font-size: 32px;
  }
}

.centered {
  text-align: center;
}

.subtitle {
  color: var(--main-color);
  font-family: 'Raleway';
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  margin-bottom: 75px;
  p:not(:last-child) {
    margin-bottom: 20px;
  }
  @media (max-width: 480px){
      font-size: 16px;
      line-height: 140%;
      margin-bottom: 50px;
  }
}

.banner-promo {
  &_blog {
    padding-bottom: 130px !important;
  }
  .subtitle {
    margin: 0;
    margin-top: 25px;
    color: var(--light-color);
  }
}
