// Header start ========================================================================================================================================================

.all_pages {
  position: fixed;
  top: 20%;
  right: 0;
  z-index: 101;
  &:hover {
    ul {
      right: 0;
    }
  }
  .page_point {
    padding: 10px 15px;
    background: #00d2ff;
    cursor: pointer;
    color: #fff;
  }
  ul {
    background: #00d2ff;
    padding: 15px 20px;
    position: absolute;
    top: 100%;
    width: 200px;
    right: -200px;
    transition: 0.4s;
    max-height: 450px;
    overflow-y: auto;
    li {
      list-style: none;
      margin-bottom: 15px;
      font-size: 16px;
      a {
        color: #fff;
      }
    }
  }
}

.wow {
  visibility: hidden;
}

body {
  background-color: #F8F8F8;
  &.lock {
    overflow: hidden;
  }
}
.header {
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: rgba(#ffffff, 1);
  background-color: #F8F8F8;
  margin: 0 auto;
  transition: all .4s ease 0s;
  @media (max-width: 992px){
      padding: 10px 0;
  }
  &.scroll {
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.2);
    padding: 5px 0;
    .header__logo {
      svg {
        transition: all 0.3s ease 0s;
      }
    }
  }

  // .header__wrap

  &__wrap {
    display: flex;
    align-items: center;
    @media (max-width: 992px){
        justify-content: space-between;
    }
    @media (max-width: 992px){
      > .header__admin {
        a {
          font-size: 0;
          border: none;
        }
      }
    }
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 101;
    transition: all 0.3s ease 0s;
    margin-right: 110px;
    width: 131px;
    @media (max-width: 1400px){
        margin-right: 50px;
    }
    @media (max-width: 992px){
        width: auto;
    }
    svg {
      transition: all 0.3s ease 0s;
      path, g, rect {
        transition: all 0.3s ease 0s;
      }
    }
    @media (max-width: 576px){
      transition: all 0.3s ease 0s;
        svg {
          max-width: 90px;
          height: auto;
        }
    }
  }

  @keyframes rotateLogo {
    0% {
      transform: rotateY(0deg);
    }
    25% {
      transform: rotateY(180deg);
    }
    50% {
      transform: rotateY(360deg);
    }
    75% {
      transform: rotateY(720deg);
    }
    100% {
      transform: rotateY(360deg);
    }
  }

  // .header__admin

  &__admin {
    margin-left: auto;
    a.header__login {
      color: var(--main-color);
      margin-right: 40px;
      @media (max-width: 1200px){
          margin-right: 20px;
      }
      @media (max-width: 576px){
          margin-right: 0;
      }
    }
    @media (max-width: 576px) or (max-height: 400px){
      margin-right: 0;
      display: flex;
      width: 100%;
      text-align: center;
    }
    a {
      display: inline-block;
      font-family: 'Raleway';
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
    @media (max-width: 992px){
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 7%;
        @media (max-width: 576px) or (max-height: 400px){
            bottom: 7%;
        }
        a {
          padding: 12px 24px;
          border-radius: 12px;
          color: var(--light-color) !important;
          border: 2px solid var(--light-color);
          @media (max-width: 576px) or (max-height: 400px){
              border: none;
              border-radius: 0;
              flex-basis: 50%;
          }
          &:after {
            content: none;
        }
      }
    }
  }

  // .header__login

  &__login {
    position: relative;
    &:after {
      content:'';
      opacity: 0;
      transform: translateX(-50%);
      position: absolute;
      left: 50%;
      bottom: -18px;
      width: 12px;
      height: 12px;
      background-color: var(--accent-color);
      border-radius: 50%;
      transition: all 0.3s ease 0s;
    }
    &:hover:after {
      opacity: 1;
    }
  }

  // .header__sign

  &__sign {
    padding: 12px 24px;
  }

  // Nav list ========================================================================================================================================================

  .nav {
    @media (max-width: 992px){
      position: fixed;
      left: 0;
      top: 0;
      z-index: 100;
      overflow-y: auto;
      width: 100%;
      height: 100vh;
      background-color: rgb(220, 135, 135);
      transition: all 0.3s ease 0s;
      transform-origin: 100% 0%;
      transform: scale(0);
      &.menu--active {
        transform: scale(1);
      }
    }
    > ul {
      display: flex;
      @media (max-width: 992px){
          display: flex;
          flex-direction: column;
          margin-left: 0;
          align-items: center;
          justify-content: center;
          background: linear-gradient(0deg, #F0642F 0%, #8F663E 28.97%, #306100 101.1%);
          position: relative;
      }
      > li {
        &:not(:last-child) {
          margin-right: 56px;
          @media (max-width: 1400px){
              margin-right: 30px;
          }
          @media (max-width: 1200px){
              margin-right: 20px;
          }
          @media (max-width: 992px){
              margin-right: 0;
              margin-bottom: 10px;
          }
        }
        > a {
          color: var(--main-color);
          font-family: 'Raleway';
          font-style: normal;
          font-weight: 500;
          line-height: normal;
          position: relative;
          @media (max-width: 992px){
              font-size: 24px;
              color: var(--light-color);
          }
          @media (max-height: 400px){
              font-size: 18px;
          }
          @media (min-width: 993px){
            &.active {
              &:after {
                content:'';
                opacity: 1;
                transform: translateX(-50%);
                position: absolute;
                left: 50%;
                bottom: -18px;
                width: 12px;
                height: 12px;
                background-color: var(--accent-color);
                border-radius: 50%;
              }
            }
          }
          &:after {
            content:'';
            transition: all 0.5s ease 0s;
            transform: translateX(-50%);
            opacity: 0;
            position: absolute;
            left: 50%;
            bottom: -18px;
            width: 12px;
            height: 12px;
            background-color: var(--accent-color);
            border-radius: 50%;
          }
          @media (min-width: 993px){
            &:hover {
              &:after {
                opacity: 1;
              }
            }
          }
        }
      }
      @media (max-width: 992px){
          width: 100%;
          height: 100%;
      }
    }
  }

  .burger {

    @include burger;
    display: none;

    @media (max-width: 992px){
      display: block;
