.commission {
  margin-top: var(--header-height);
  padding-top: 134px;
  padding-bottom: 134px;
  .subtitle {
    max-width: 1024px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 100px;
    @media (max-width: 576px){
        margin-bottom: 40px;
    }
  }
  @media (max-width: 992px){
      padding-top: 80px;
      padding-bottom: 80px;
  }
  @media (max-width: 576px){
      padding-top: 60px;
  }

  // .commission__title

  &__title {
    text-align: center;
    max-width: 1400px;
    margin: 0 auto 24px auto;
    font-size: 64px;
    font-weight: 600;
    line-height: normal;
    @media (max-width: 1400px){
        font-size: 55px;
    }
    @media (max-width: 1100px){
        font-size: 42px;
        max-width: 90%;
    }
    @media (max-width: 992px){
        font-size: 36px;
        max-width: 90%;
    }
    @media (max-width: 768px){
        max-width: 100%;
    }
    @media (max-width: 480px){
        font-size: 32px;
    }
    @media (max-width: 400px){
        font-size: 28px;
    }
  }

  // .commission__subtitle

  &__subtitle {
    max-width: 1024px;
    margin: 0 auto 70px auto;
    color: var(--main-color);
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    @media (max-width: 1400px){
        margin-bottom: 50px;
    }
    @media (max-width: 400px){
        font-size: 16px;
    }
  }

  // .commission__list

  &__list {
    counter-reset: list;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 1fr;
    grid-auto-rows: 1fr;
    gap: 24px;
    margin-bottom: 80px;
    padding-left: 0;
    @media (max-width: 1200px){
        grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 992px){
        gap: 12px;
    }
    @media (max-width: 768px){
        grid-template-columns: 1fr;
        justify-items: center;
        gap: 20px;
    }
  }

  // .commission__item

  &__item {
    position: relative;
    border-radius: 12px;
    border: 2px solid var(--light-color);
    background: linear-gradient(253deg, #FFF9E9 3.67%, #FFF 100.61%);
    box-shadow: 0px 10px 80px 0px rgba(0, 0, 0, 0.10);
    padding: 66px 22px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease 0s;
    &:hover {
      background: linear-gradient(253deg, #fff3cf 3.67%, #FFF 100.61%);
      box-shadow: 0px 10px 80px 0px rgba(0, 0, 0, 0.40);
      &:after {
        transform: scale(1.1);
      }
    }
    @media (max-width: 768px){
        width: 100%;
        max-width: 490px;
    }
    @media (max-width: 576px){
        padding: 50px 20px;
    }
    &:after {
      transition: all 0.3s ease 0s;
      counter-increment: list;
      content: counter(list);
      width: 64px;
      height: 64px;
      position: absolute;
      top: 22px;
      right: 22px;
      background-image: url('../img/bg_coin.svg');
      background-size: 64px 64px;
      background-repeat: none;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #FFCC4D;
      text-shadow: 0px 2px 0px #CB801E;
      font-size: 32px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      @media (max-width: 480px){
        background-size: 50px 50px;
        font-size: 24px;
        width: 50px;
        height: 50px;
        top: 10px;
        right: 10px;
      }
    }
  }

  // .commission__level

  &__level, &__ngr {
    color: var(--main-color);
    text-align: center;
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    text-transform: uppercase;
    margin-bottom: 10px;
    @media (max-width: 400px){
        margin-bottom: 5px;
        font-size: 20px;
    }
  }

  // .commission__ftd

  &__ftd {
    color: var(--accent-color);
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 10px;
    text-align: center;
    @media (max-width: 400px){
        font-size: 28px;
        margin-bottom: 5px;
    }
  }

  // .commission__btn

  &__btn {
    padding: 20px 57px;
    margin-bottom: 128px;
    @media (max-width: 1200px){
        margin-bottom: 80px;
    }
  }

  // .commission__text

  &__text {
    max-width: 1400px;
    margin: 0 auto;
    color: var(--main-color);
    text-align: center;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    @media (max-width: 768px){
        font-size: 26px;
        line-height: 130%;
    }
    @media (max-width: 400px){
        font-size: 22px;
    }
  }

  // .commission__wrap

  &__wrap {
    .commissions {
      background-color: var(--light-color);
    }
  }
}


