.search {
  background-color: var(--light-color);
  ._container {
    width: 100%;
    position: relative;
    margin: 0 auto;
  }

  // .search__wrap

  &__wrap {
    position: absolute;
    left: 15px;
    right: 15px;
    z-index: 1;
    transform: translateY(-50%);
    border-radius: 12px;
    border: 2px solid var(--light-color);
    background-color: var(--light-color);
    box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.08);
    padding: 25px 48px;
    @media (max-width: 768px){
        padding: 20px;
    }
    @media (max-width: 400px){
        padding: 15px 10px;
        right: 5px;
        left: 5px;
    }
  }

  // .search__form

  &__form {
    position: relative;
    margin-bottom: 12px;
    form {
      display: flex;
      input {
        border: none;
        color: #001140;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        padding: 8px 24px;
        border-bottom: 1px solid rgba(240, 100, 47, 0.30);
        @media (max-width: 480px){
            font-size: 14px;
            line-height: 26px;
            padding: 6px 24px;
        }
        &:focus {
          border-bottom: 0;
          outline: 1px solid rgba(240, 100, 47, 0.60);
        }
        &::placeholder {
        color: rgba(#001140, 0.5)
        }
      }
    }
    button {
      outline: none;
      border: none;
      background-color: transparent;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
    }
  }

  // .search__input

  &__input {
    width: 100%;
    input {
      width: 100%;
    }
  }

  // .search__sort

  &__sort {
    display: flex;
    column-gap: 8px;
    row-gap: 8px;
    flex-wrap: wrap;
    @media (max-width: 480px){
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: auto;
        gap: 6px;
    }
    span {
      color: rgba(#001140, 0.5);
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 125%;
      margin-right: 14px;
      grid-column: 1 / 4;
    }
    input[type="radio"] {
      display: none;
    }

    label {
      display: inline-block;
      color: var(--accent-color);
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 125%;
      padding: 4px 16px;
      cursor: pointer;
      border-radius: 4px;
