  border-radius: 8px;
}

.faq .accordion {
  padding: 32px 15px 32px 15px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
  border-radius: 0px;
  background: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  color: #001140;
  font-size: 24px;
