    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
  }
}

.faq .accordion.active {
  padding-bottom: 11px;
}

@media (max-width: 992px){
  .faq .accordion {
    padding-top: 20px;
    padding-bottom: 20px;
    @media (max-width: 576px){
        padding-top: 15px;
        padding-bottom: 15px;
    }
  }
}

.faq .active, .faq .accordion:hover {
  background-color: none;
}

.faq .active i {
  transform: rotate(90deg);
}

.faq .accordion.active .vertical {
  background-color: var(--accent-color);
}

.faq .accordion h2 {
  @media (max-width: 992px){
      display: block;
      max-width: 90%;
      font-size: 20px;
      line-height: 120%;
  }
  @media (max-width: 576px){
      max-width: 85%;
      font-size: 18px;
  }
  @media (max-width: 480px){
      font-size: 16px;
  }
}

.faq .accordion.active h2 {
  color: var(--accent-color);
}

.faq:hover div h2{
  transition: all 0.3s ease 0s;
  color: var(--accent-color);
}

.faq:hover div {
  color: var(--accent-color);
}

.faq:hover .circle-animation .vertical,
.faq:hover .circle-animation .horizontal {
  background-color: var(--accent-color);
}

.faq .panel {
  max-height: 0;
  overflow: hidden;
  visibility: hidden;
  transition-duration: 400ms;
  transition-property: max-height, visibility;
  transition-delay: 0, 400ms;
  padding-right: 50px;
  padding-left: 15px;
  @media (max-width: 992px){
      max-width: 90%;
  }
  @media (max-width: 576px){
      max-width: 100%;
      padding-right: 25px;
  }
}

.panel.open {
  visibility: visible;
  transition-duration: 400ms;
  transition-property: max-height, visibility;
  transition-delay: 400ms, 0;
  box-sizing: content-box;
  padding-bottom: 25px;
}

.closed .vertical {
  transition: all 0.4s ease-in-out;
  transform: rotate(-90deg);
}

.closed .horizontal {
  transition: all 0.4s ease-in-out;
  transform: rotate(-90deg);
  opacity: 1;
}

.active {
  opacity: 1;
}

.active .vertical {
  transition: all 0.4s ease-in-out;
  transform: rotate(90deg);
}

.active .horizontal {
  transition: all 0.4s ease-in-out;
  transform: rotate(90deg);
  opacity: 0;
}

.circle-animation {
  border: 1px solid #dddddd;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  position: relative;
  flex: 0 0 32px;
  @media (max-width: 480px){
      width: 24px;
      height: 24px;
      flex: 0 0 24px;;
  }
}

.circle-animation .horizontal {
  position: absolute;
  background-color: rgba(#001140, 0.5);
  width: 9px;
  height: 1px;
  left: 50%;
  top: 50%;
  margin-left: -4px;
}

.circle-animation .vertical {
  position: absolute;
  background-color: rgba(#001140, 0.5);
  width: 1px;
  height: 9px;
  left: 50%;
  top: 50%;
  margin-top: -4px;
}

.panel p {
  padding: 7px 0;
  color: #001140;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 31px;
  box-sizing: border-box;
}

@media (max-width: 576px){
  .panel p {
    font-size: 14px;
    line-height: 24px;
    padding: 5px 0;
  }
}

