      color: rgba(#272E3E, 0.7);
      font-style: normal;
      font-weight: 400;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        top: 5px;
        width: 8px;
        height: 8px;
        background-color: var(--accent-color);
        border-radius: 50%;
      }
    }
  }
  ol {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 24px;
    counter-reset: list;
    > li {
      margin-bottom: 12px;
      position: relative;
      padding-left: 30px;
      color: rgba(#272E3E, 0.7);
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      &:after {
        counter-increment: list;
        content: counter(list, decimal-leading-zero);
        position: absolute;
        left: 0;
        top: 0;
        color: var(--accent-color);
      }
    }
  }
  a {
    color: var(--accent-color);
    &:hover {
      text-decoration: underline;
    }
  }
}

// Page privacy policy end ========================================================================================================================================================

// Testimonials page start ========================================================================================================================================================

.testimonials {
  padding-top: 117px;
  padding-bottom: 96px;
  background-color: #f8f8f8;
  @media (max-width: 992px){
      padding-top: 80px;
      padding-bottom: 80px;
  }

  // .testimonials__wrap

  &__wrap {
    .contact {
      background-color: var(--light-color);
      padding-top: 100px;
    }
  }

  // .testimonials__items

  &__items {
    margin-bottom: 72px;
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  // .testimonials__item

  &__item {
    flex-basis: calc(50% - 12px);
    border-radius: 8px;
    background: var(--light-color);
    box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.08);
    padding: 32px;
    transition: all 0.3s ease 0s;
    &:hover {
      box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.28);
      .testimonials__link p {
        color: var(--accent-color);
      }
    }
    @media (max-width: 1400px){
        padding: 24px;
    }
    @media (max-width: 992px){
        flex-basis: 100%;
        max-width: 750px;
        margin: 0 auto;
    }
    @media (max-width: 400px){
        padding: 18px;
    }
  }

  // .testimonials__link

  &__link {
    width: 100%;
    height: 100%;
  }

  // .testimonials__title

  &__title {
    color: #001140;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-bottom: 16px;
    transition: all 0.3s ease 0s;
    @media (max-width: 576px){
        font-size: 22px;
        line-height: 26px;
        margin-bottom: 12px;
    }
  }

  // .testimonials__text

  &__text {
    color: rgba(#272E3E, 0.7);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    @media (max-width: 992px){
        font-size: 20px;
    }
  }


  // .testimonials__btn

  &__btn {
    padding: 20px 48px;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    &:active {
      transform: translate(2px, 2px);
    }
    &:focus {
      outline: 1px solid var(--accent-color);
    }
  }
}


// Testimonials page end========================================================================================================================================================

// Footer start ========================================================================================================================================================

.footer {
  padding-top: 58px;
  padding-bottom: 72px;
  background-color: var(--main-color);
  @media (max-width: 1400px){
      padding-bottom: 30px;
  }

  // .footer__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-bottom: 27px;
    @media (max-width: 1200px){
        column-gap: 15px;
    }
    @media (max-width: 992px){
        flex-wrap: wrap;
        row-gap: 40px;
    }
    @media (max-width: 480px){
        justify-content: center;
    }
  }

  // .footer__block

  &__block {
    &:first-child {
      flex-basis: 26%;
      @media (max-width: 1200px){
        flex-basis: 20%;
      }
    }
    &:last-child {
      flex-basis: 14%;
      @media (max-width: 1200px){
        flex-basis: 20%;
      }
      > ul {
        display: flex;
        justify-content: flex-end;
        column-gap: 8px;
        > li {
          width: 32px;
          height: 32px;
          border-radius: 5px;
          border: 1px solid rgba(240, 100, 47, 0.30);
          position: relative;
          transition: all 0.3s ease 0s;
          &:hover {
            transform: translateY(-3px);
          }
          > a {
            width: 32px;
            height: 32px;
            width: 100%;
            height: 100%;
            img {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
            }

          }
        }
      }
    }
    &:nth-child(3) {
      padding-left: 13px;
      @media (max-width: 1200px){
          padding-left: 0;
      }
    }
    &:nth-child(4) {
      padding-left: 28px;
      @media (max-width: 1200px){
          padding-left: 0;
      }
    }
    flex-basis: 20%;
    @media (max-width: 1200px){
      flex-basis: 20%;
    }
    &:nth-child(4) {
      ul {
        &:nth-child(2) {
          margin-bottom: 45px;
        }
      }
    }
    p {
      color: var(--accent-color);
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: 24px;
      margin-bottom: 8px;
    }
    > ul {
      > li {
        @media (max-width: 480px){
            margin-bottom: 0;
        }
        > a {
          color: var(--light-color);
          font-size: 14px;
          font-style: normal;
          font-weight: 500;
          line-height: 32px;
          opacity: 0.5;
          transition: all 0.3s ease 0s;
          @media (max-width: 480px){
              line-height: 30px;
          }
        }
      }
    }
    &:not(:last-child, :first-child) {
      > ul {
        > li {
          > a {
            position: relative;
            transition: all 0.3s ease 0s;
            &:after {
              content: '';
              position: absolute;
              left: 0;
              bottom: -3px;
              width: 0%;
              height: 2px;
              background-color: var(--light-color);
              opacity: 0.5;
              transition: all 0.3s ease 0s;
            }
            &:hover {
              &:after {
                transition: all 0.3s ease 0s;
                width: 100%;
              }
            }
          }
        }
      }
    }
    @media (max-width: 768px){
        flex-basis: calc(50% - 24px);
    }
    @media (max-width: 480px){
      flex-basis: 100%;
      text-align: center;
    }
  }

  // .footer__brands

  &__brands {
    color: var(--light-color) !important;
  }

  // .footer__copy

  &__copy {
    display: flex;
    justify-content: space-between;
    color: rgba(255,255,255, 0.5);
    font-size: 12px;
    font-style: normal;
    font-weight: 300;
    line-height: 16px;
    @media (max-width: 480px){
        flex-direction: column;
        row-gap: 5px;
        align-items: center;
    }
    a {
      color: rgba(255,255,255, 0.5);
      transition: all 0.3s ease 0s;
      &:hover {
        color: rgba(255,255,255, 1);
      }
    }
  }
}

// Footer end ========================================================================================================================================================

// Keyframes ========================================================================================================================================================

@keyframes heroBgText {
  0%, 10%, 100% {
    background-position: -40rem 0;
  }
  65%, 85% {
    background-position: 0 0;
  }
}

@keyframes heroCursorText {
  0%, 10%, 100% {
    width: 0;
  }
  65%, 78%, 85% {
    width: 100%;
    opacity: 1;
  }
  75%, 81% {
    opacity: 0;
  }
}

@keyframes gradient {
  0% {
      background-position: 100% 0%;
  }
  50% {
      background-position: 40% 0%;
  }
  100% {
      background-position: 100% 0%;
  }
}

@keyframes gradientBrands {
  0% {
      background-position: 100% 0%;
  }
  50% {
      background-position: 0% 0%;
  }
  100% {
      background-position: 100% 0%;
  }
}

// Animated heading 1 ========================================================================================================================================================

.accent {
  animation: 1.5s blackColor linear 1.5s infinite alternate;
}
.black {
  animation: 1.5s accent linear 0s infinite alternate;
}
.rotate {
  transform-origin: 13.5% 50%;
  animation: 2s rotate linear 0s infinite alternate;
}
.rotate-phone {
  transform-origin: 18.5% 50%;
  animation: 2s rotate linear 0s infinite alternate;
}

.money {
  transition: all 0.3s ease 0s;
}
@keyframes rotate {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(360deg);
  }
}
@keyframes accent {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.4;
  }
}
@keyframes blackColor {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.4;
  }
}

@media (min-width: 1601px) {
  .text-animate {
    position: relative;
    max-width: max-content;

  }

  .text-animate h1 {
    text-shadow: 0px 0px 1px rgba(0,0,0,0.15);
    color: transparent;
    background-image: linear-gradient(var(--main-color), var(--main-color));
    background-repeat: no-repeat;
    -webkit-background-clip: text;
    background-position: 0 0;
    animation: heroBgText 6s linear infinite;
  }

  .text-animate h1::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0px;
    width: 0px;
    height: 80%;
    border-right: 1px solid var(--main-color);
    z-index: 1;
    animation: heroCursorText 6s linear infinite;
  }
}


