:root {
  // base
  --font-family: "WorkSans", sans-serif;
  --content-width: 1520px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --main-color: #272E3E;
  --accent-color: #F0642F;
  --light-color: #fff;
  --scrollTop: 0;

  // header-height

  --header-height: 130px;
  @media (max-width: 992px){
    --header-height: 110px;
  }
  @media (max-width: 992px){
    --header-height: 83px;
  }
}
